<template>
<div class="container">
    <input
      type="text"
      class="todo-input"
      placeholder="Add a person"
      v-model="newTodo"
      @keyup.enter="addTodo"
    >

    <div class="items-container">
        <div class="todo-item" v-for="(todo , index) in todos" :key="todo.id" :class="{ lightgreen : todo.id%2==1 , green : todo.id%2==0 }">

            <div class="todo-item-left">
                <div class="todo-item-label-edit-checkbox">
                    <div class="todo-item-label" v-if="!todo.editing" @click="editTodo(todo)">{{todo.title}}</div>

                    <input v-else class="todo-item-edit" type="text" v-model="todo.title" @blur="doneEdit(todo)" @keyup.enter="doneEdit(todo)" @keyup.esc="cancelEdit(todo)" v-focus>
          </div>
                    <div class="item-days-row">

                        <div class="days-row">
                            <input type="checkbox" id="sunday" value="  Sun  " v-model="todo.checkedDays" >
                            <label for="sunday">&nbsp;Sun&nbsp;</label>
                            <input type="checkbox" id="monday" value="  Mon  " v-model="todo.checkedDays">
                            <label for="monday">&nbsp;Mon&nbsp;</label>
                            <input type="checkbox" id="tuseday" value="  Tus  " v-model="todo.checkedDays">
                            <label for="tuseday">&nbsp;Tus&nbsp;</label>
                            <input type="checkbox" id="wednesday" value="  Wed  " v-model="todo.checkedDays">
                            <label for="wednesday">&nbsp;Wed&nbsp;</label>
                            <input type="checkbox" id="thursday" value="  Thu  " v-model="todo.checkedDays">
                            <label for="thursday">&nbsp;Thu&nbsp;</label>
                            <input type="checkbox" id="friday" value="  Fri  " v-model="todo.checkedDays">
                            <label for="friday">&nbsp;Fri&nbsp;</label>
                            <input type="checkbox" id="saturday" value="Sat" v-model="todo.checkedDays">
                            <label for="saturday">&nbsp;Sat&nbsp;</label>

                        </div>
                    </div>
                </div>

                <div class="item-right">

                    <div class="random-room">{{todo.roomNumber}}</div>

                    <div class="remove-item" @click="removeItem(index)">
                        <font-awesome-icon icon="trash-alt" />
                    </div>

                </div>

            </div>
        </div>

        <div class="items-container">
            <div class="todo-item" v-for="(todo) in todos" :key="todo.id" :class="{ lightgreen : todo.id%2==1 , green : todo.id%2==0 }">

                <div class="todo-item-left">
                    <div class="todo-item-label-edit-checkbox">
                        <div class="todo-item-label">{{todo.title}}</div>

                    </div>
                    <div class="item-days-row">
                        <div class="item-days" v-for="(day, index) in todo.checkedDays" :key="index">
                            {{day}}&nbsp;
                        </div>
                    </div>
                </div>

                <div class="item-right">

                    <div class="random-room">
                        Room
                        <br>
                        {{todo.roomNumber}}</div>

                </div>

            </div>
        </div>

    </div>
</template>

<script>
export default {
    name: "todo-list",
    data() {
        return {
            newTodo: '',
            titleCache: '',
            idForTodo: 3,
            todos: [{
                    id: 1,
                    title: "Dr. Shay",
                    roomNumber: 100,
                    checkedDays: [],
                    completed: false,
                    editing: false,
                },
                {
                    id: 2,
                    title: "Dr. Guy",
                    roomNumber: 100,
                    checkedDays: [],
                    completed: false,
                    editing: false,
                }
            ]
        };
    },
    directives: {
        focus: {
            inserted: function (el) {
                el.focus()
            }
        }
    },
    methods: {
        addTodo() {
            if (this.newTodo.trim().length == 0) {
                return;
            }
            this.todos.push({
                id: this.idForTodo,
                title: this.newTodo,
                completed: false,
                editing: false,
                checkedDays: [],
                roomNumber: 100,
            });
            this.idForTodo++;
            this.newTodo = '';
        },
        removeItem(index) {
            this.todos.splice(index, 1);
        },
        editTodo(todo) {
            todo.editing = true;
            this.titleCache = todo.title;

        },
        doneEdit(todo) {
            if (todo.title.trim().length == 0) {
                return;
            }
            todo.editing = false;
        },
        cancelEdit(todo) {
            todo.title = this.titleCache;
            todo.editing = false;
            this.titleCache = '';
        },
        randomNumber: function () {
            return Math.floor(Math.random() * (9) + 10) * 40;
        },
    }
};
</script>

<style lang="scss">
.days-row {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 18px;
    color: #2c3e50;
}

.todo-input {
    width: 100%;
    font-size: 18px;
    padding: 10px;
    margin-top: 10px;
    margin-bottom: 10px;

    &:focus {
        outline: 0;
    }
}

.todo-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 24px;
    color: #2c3e50;
    padding: 10px;
}

.todo-item-label {
    border: 1px solid rgba(255, 255, 255, 0);
}

.todo-item-edit {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    border: 1px solid #ccc;
    font-size: 24px;
    width: 100%;

    color: #2c3e50;

    &:focus {
        outline: 0;
    }

}

.todo-item-left {
    align-items: center;
    margin-left: 10px;
}

.todo-item-label-edit-checkbox {
    display: flex;
    align-items: center;
}

.remove-item {
    cursor: pointer;
    margin-right: 12px;
    font-size: 22px;
    align-items: center;

    &:hover {
        color: red;
        transition: 0.5s;
    }
}

.green {
    border: 1px solid lightgrey;
}

.lightgreen {
    border: 1px solid lightgrey;
}

.items-container {
    // background-image svg
}

.item-days {
    margin-bottom: 12px;
}

.item-days-row {
    display: flex;
}

.item-right {
    display: flex;
}

.random-room {
    margin-right: 24px;
    text-align: center;

}
</style>



